html {
  box-sizing: border-box;
  width: 100vw;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  padding: 60px;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  color: #212121;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

p {
  margin: 0;
  margin-bottom: 0;
}

/*
 * Стили компонента App
 */
.App {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 16px;
  padding-bottom: 24px;
}

/*
 * Стили компонента Searchbar
 */
.Searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

/*
 * Стили компонента SearchForm
 */
.SearchForm {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
}

.SearchForm-button {
  display: inline-block;
  width: 48px;
  height: 48px;
  border: 0;
  background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.SearchForm-button:hover {
  opacity: 1;
}

.SearchForm-button-label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.SearchForm-input {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
}

.SearchForm-input::placeholder {
  font: inherit;
  font-size: 18px;
}

/*
 * Стили компонента ImageGallery
 */
.ImageGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

/*
 * Стили компонента ImageGalleryItem
 */
.ImageGalleryItem {
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.ImageGalleryItem-image {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem-image:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}

/*
 * Стили компонента Modal
 */

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Modal {
  max-width: calc(100vw - 148px);
  max-height: calc(100vh - 124px);
  overflow: hidden;
}

/*
* Стили компонента Buton (Load more)
*/
.Button {
  padding: 8px 16px;
  border-radius: 2px;
  background-color: #3f51b5;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 180px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.Button:hover,
.Button:focus {
  background-color: #303f9f;
}

.photo-card {
  position: relative;
  display: block;
  overflow: hidden;
  object-fit: cover;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
  width: 370px;
  min-height: 400px;
  height: 400px;

  padding: 20px;
}

.photo-card__img {
  height: 300px;
  display: block;
  width: 100%;
  object-fit: cover;
}

.photo-card:hover,
.photo-card:focus {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
}

.stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.stats-item {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.stats-item:hover,
.stats-item:focus {
  color: #ffc107;
  cursor: pointer;
}

.material-icons {
  margin-right: 8px;
}

.photo-card:hover,
.photo-card:focus {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
}

.stats {
  background-color: rgba(255, 255, 255, 1);
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1680px;
  margin: 0 auto;
  padding: 8px;

  background-color: rgba(255, 255, 255, 0.8);
}

@media screen and (max-width: 876px) {
  .gallery {
    background-color: transparent;
  }
}

.gallery-item {
  margin: 15px;
}

.header {
  font-family: 'Lobster';
  position: fixed;
  top: 0;
  left: 0;
  z-index: 500;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 80px;
  color: #ffc107;

  background-image: url(https://i.ytimg.com/vi/8yny_PR2IOo/maxresdefault.jpg);
  background-size: contain;
}

.title {
  font-size: 32px;
  margin: 0;
}
@media screen and (max-width: 550px) {
  .title {
    font-size: 30px;
  }
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 15px;
  }
}

.great {
  margin: auto;
  text-align: center;
  max-width: 500px;
  font-family: 'Lobster';
}
.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-form {
  display: flex;
}

.form-input {
  margin-right: 8px;
}
@media screen and (max-width: 550px) {
  .form-input {
    max-width: 120px;
  }
}
@media screen and (max-width: 320px) {
  .form-input {
    max-width: 90px;
  }
}

.btn-sub {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lobster';
}
.btn-wrapper {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.is-hidden {
  position: absolute;
  top: 100;
  left: 100;
  margin: -1px;
  padding: -1px;
  opacity: 0;
}

.imgModal {
  max-width: calc(100vw - 148px);
  max-height: calc(100vh - 124px);
  object-fit: cover;
}
